import React from "react";
import { Checkbox, Divider, Menu } from "antd";

const TableMenuItem = ({ onColHide, onMenuItemClick, menuItems, columns }) => {
  return (
    <Menu onClick={onMenuItemClick}>
      {menuItems.map(menu => (
        <>
          <Menu.Item key={menu.type}>{menu.title}</Menu.Item>
          <Divider className="m-0" />
        </>
      ))}
      {columns && (
        <Menu.ItemGroup key="table_column" title="Column">
          {columns.map(item => (
            <>
              <Menu.Item key={item.key}>
                <Checkbox checked={item.isVisible} onChange={() => onColHide(item)}>
                  {item.title}
                </Checkbox>
              </Menu.Item>
              <Divider className="m-0" />
            </>
          ))}
        </Menu.ItemGroup>
      )}
    </Menu>
  );
};

export default TableMenuItem;
