import React from "react";
import { TreeSelect } from "antd";
import { TreeNode } from "antd/lib/tree-select";
import { noop } from "lodash";

const ALL = "All";

const LocationPicker = ({ locations, name, form, placeholder, onValueChange = noop, dataTestId}) => {
  const selectedLocation = form.getFieldValue(name);

  const onLocationChange = (value, label, extra) => {
    const { triggerValue, checked } = extra;
    if (triggerValue === ALL) {
      const allLocation = checked ? [ALL] : [];
      form.setFieldsValue({ city: allLocation });
    } else {
      const newLocations = value.filter(val => val !== ALL);
      form.setFieldsValue({ city: newLocations });
    }

    onValueChange();
  };

  return (
    <TreeSelect
      data-test-id={dataTestId || name}
      showSearch
      style={{ width: "100%" }}
      dropdownStyle={{ maxHeight: 400, overflow: "auto" }}
      allowClear
      multiple
      treeDefaultExpandAll
      placeholder={placeholder}
      onChange={onLocationChange}
      treeCheckable
      value={selectedLocation}
    >
      <TreeNode value={ALL} title={ALL} />
      {locations.map(({ country, locations }) => (
        <TreeNode value={country} title={country} key={country}>
          {locations.map(({ locationCode, locationName }) => (
            <TreeNode value={locationName} title={locationName} />
          ))}
        </TreeNode>
      ))}
    </TreeSelect>
  );
};

export default LocationPicker;
