import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import { TABLE_COLUMNS } from "constants/HeadCountReportConstant";
import Utils from "utils";
const headCountService = {};

headCountService.preparedHeadersForPDF = (tableColumns = []) =>
  tableColumns.map(col => {
    return { key: col.dataIndex, value: col.dataIndex };
  });

headCountService.preparedDataForExport = (data = []) => {
  const totalObj = {
    City: "Total",
  };
  const _data = data.map(obj => {
    const _obj = {};
    Object.keys(obj).forEach(key => {
      if (!isNaN(obj[key])) {
        const total = totalObj[key] ? +totalObj[key] + obj[key] : obj[key];
        totalObj[key] = total.toString();
        _obj[key] = obj[key].toString();
      } else {
        _obj[key] = obj[key];
      }
    });
    return _obj;
  });
  const total = JSON.parse(JSON.stringify(totalObj));
  _data.push(total);
  return _data;
};

headCountService.getHeadCountOfCities = async data => {
  const { path, method } = API_CONSTANTS.GET_HEAD_COUNT_REPORT;
  try {
    const response = await service({
      url: path,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the country list.", e);
  }
};

headCountService.prepareTableData = citiesObj => {
  const data = [];
  const columnsKeys = { City: "City" };
  const statusSummary = {};
  Object.keys(citiesObj).forEach(city => {
    const item = {};
    let citySummary = 0;
    citiesObj[city].forEach(obj => {
      item[obj.allocationStatus] = obj.headCount;
      citySummary += obj.headCount;
      if (!statusSummary[obj.allocationStatus]) statusSummary[obj.allocationStatus] = 0;
      statusSummary[obj.allocationStatus] += obj.headCount;
      columnsKeys[obj.allocationStatus] = obj.allocationStatus;
    });
    data.push({
      [TABLE_COLUMNS.CITY]: city,
      [TABLE_COLUMNS.CITY_SUMMARY]: citySummary,
      ...item,
    });
  });
  columnsKeys[TABLE_COLUMNS.CITY_SUMMARY] = TABLE_COLUMNS.CITY_SUMMARY;
  data.forEach(obj => {
    Object.keys(columnsKeys).forEach(key => {
      if (!obj[key]) {
        obj[key] = 0;
      }
    });
  });
  return [data, makeColumnKeys(columnsKeys), statusSummary];
};

const makeColumnKeys = columnsKeys => {
  return Object.keys(columnsKeys).map(key => ({ name: key }));
};

export default headCountService;
