import { MenuFoldOutlined, SearchOutlined, QuestionCircleTwoTone } from "@ant-design/icons";
import { Button, Card, Col, Drawer, Form, PageHeader, Row, Select, Table, Tooltip, Popover, DatePicker } from "antd";
import LocationPicker from "components/shared-components/LocationPicker";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import {
  DATE_FORMAT,
  FILTER_FORM_FIELDS,
  REPORTS_DESCRIPTION,
  SEARCH,
  SELECT_METHODS,
  SELECT_TEXT,
} from "constants/CommonConstants";
import {
  ACCOUNT,
  CHART_FORM_NAME,
  CHILDREN,
  CITY,
  EXPORT_FILE_NAME,
  MENU_ITEMS,
  MENU_ITEMS_TYPES,
  NO_DATA_MESSAGE,
  TABLE_COLUMNS,
} from "constants/HeadCountReportConstant";
import useIntlMessage from "hooks/useIntlMessage";
import { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import Utils from "utils";
import FileHandler from "utils/fileHandler";
import headCountService from "../../../../services/HeadCountService";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const ALL = "All";
const { Option } = Select;

const HeadCountReport = () => {
  const getMessage = useIntlMessage();
  const [form] = Form.useForm();
  const [openPopOver, setOpenPopOver] = useState(false);
  const [open, setOpen] = useState(false);
  const [columns, setColumns] = useState([]);
  const [statusSummary, setStatusSummary] = useState({});
  const [headCountData, setHeadCountData] = useState([]);
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const dispatch = useDispatch();

  useEffect(() => {
    return () => {
      dispatch(showLoader(false));
    };
  }, []);
  const handleShowLoader = useCallback(
    (isLoading = true) => {
      dispatch(showLoader(isLoading));
    },
    [dispatch],
  );

  const updateTableColumns = columnsKeys => {
    const _columns = columnsKeys.map(column => {
      const isCitySummary = column.name === TABLE_COLUMNS.CITY_SUMMARY;
      const title = column.name === TABLE_COLUMNS.CITY_SUMMARY ? "CITY SUMMARY" : column.name;
      return {
        title: (
          <Tooltip title={title} placement="bottom">
            <span>{title}</span>
          </Tooltip>
        ),
        dataIndex: column.name,
        key: column.name,
        className: isCitySummary ? "bg-gray-lightest" : "",
        render: text => {
          return <span className={isCitySummary ? "font-weight-bolder" : ""}>{text || 0}</span>;
        },
      };
    });
    setColumns(_columns);
  };

  const fetchData = async params => {
    handleShowLoader(true);
    const citiesData = await headCountService.getHeadCountOfCities(params);
    if (citiesData) {
      const [data, columnsKeys, _statusSummary] = headCountService.prepareTableData(citiesData);
      updateTableColumns(columnsKeys);
      setHeadCountData(data);
      setStatusSummary(_statusSummary);
    }
    handleShowLoader(false);
  };

  const getStatusSummary = () => {
    let total = 0;
    let tableSummary = [];
    columns.forEach((column, index) => {
      let cellText = "";
      if (index === columns.length - 1) {
        cellText = total;
        tableSummary.push(cellText);
      }
      if (index === 0) {
        tableSummary.push("Total");
      }
      if (index !== 0 && index !== columns.length - 1) {
        total += statusSummary[column.key] || 0;
        cellText = statusSummary[column.key] || "";
        tableSummary.push(cellText);
      }
    });

    return (
      <Table.Summary>
        <Table.Summary.Row className="bg-gray-lightest">
          {tableSummary?.map((data, index) => {
            return (
              <Table.Summary.Cell className="font-weight-bolder" index={index}>
                {data}
              </Table.Summary.Cell>
            );
          })}
        </Table.Summary.Row>
      </Table.Summary>
    );
  };

  const onSearch = values => {
    if (!Utils.isEmpty(values)) {
      const params = {
        locations: values.city,
        accounts: values.account,
        onDate: values.selectDate,
      };
      fetchData(params);
    }
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.exportExcel:
        exportToExcel();
        break;
      case MENU_ITEMS_TYPES.exportPDF:
        exportToPDF();
        break;

      default:
        return;
    }
  };

  const exportToExcel = () => {
    const _headCountData = headCountService.preparedDataForExport(headCountData);
    FileHandler.exportToExcel(_headCountData, EXPORT_FILE_NAME);
    setIsMenuVisible(false);
  };

  const exportToPDF = () => {
    const headers = headCountService.preparedHeadersForPDF(columns);
    const _headCountData = headCountService.preparedDataForExport(headCountData);
    FileHandler.exportToPDF(headers, _headCountData, { fontSize: 10, padding: 3 }, 250, 200, EXPORT_FILE_NAME, 40);
    setIsMenuVisible(false);
  };

  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };
  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };
  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("sidenav.reports.head-count")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={REPORTS_DESCRIPTION.headCountReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>

      {headCountData.length === 0 ? null : (
        <Row justify="space-between" gutter={[20, 20]} className="mt-4">
          <Col lg={24} md={24}>
            <div className="ant-affix mt-2 menu-bar">
              <Button type="primary" onClick={showDrawer} shape="circle">
                <MenuFoldOutlined />
              </Button>
            </div>
          </Col>
        </Row>
      )}

      <Form {...layout} form={form} onFinish={onSearch} name={CHART_FORM_NAME} labelAlign="left" layout="horizontal">
        <Row gutter={[40]} className="mb-3">
          <Col xl={8} lg={12} md={12} xs={24}>
            <Form.Item name={CITY.name} label={CITY.label} rules={[{ required: true }]}>
              <LocationPicker
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.CITY.label)}
                locations={allLocations}
                name="city"
                form={form}
              />
            </Form.Item>
          </Col>
          <Col xl={8} lg={12} md={12} xs={24}>
            <Form.Item name={ACCOUNT.name} label={ACCOUNT.label} rules={[{ required: true }]}>
              <Select
                showSearch
                allowClear
                optionFilterProp={CHILDREN}
                mode={SELECT_METHODS.MULTIPLE}
                onSelect={value => Utils.onAccountSelection(value, form, ACCOUNT.name)}
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.ACCOUNT.label)}
              >
                <Option value={ALL}>{ALL}</Option>
                {accountsList?.map((account, index) => (
                  <Option key={index} value={account.accountName}>
                    {account.accountName}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
          <Col xl={8} lg={12} md={12} xs={24}>
            <Form.Item 
             label={getMessage(FILTER_FORM_FIELDS.SELECT_DATE.label)}
             name={FILTER_FORM_FIELDS.SELECT_DATE.name}
             rules={[{ required: true }]}
            >
              <DatePicker className={"w-100"} format={DATE_FORMAT} disabledDate={Utils.disableFutureDates} />
            </Form.Item>
          </Col>
          <Col lg={24} md={24} xs={24}>
            <Button type="primary" icon={<SearchOutlined className="mr-1" />} htmlType="submit" className="float-right">
              {SEARCH.text}
            </Button>
          </Col>
        </Row>
      </Form>

      <Card>
        {headCountData && headCountData.length > 0 ? (
          <TableComponent
            rowKey={record => record.City}
            columns={columns}
            data={headCountData}
            isBordered="true"
            tableSize="middle"
            visibleData={getStatusSummary}
            pagination={false}
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
          />
        ) : (
          <h2 className="text-center ant-empty ant-empty-normal font-size-lg">{NO_DATA_MESSAGE}</h2>
        )}
        <Drawer onClose={onClose} destroyOnClose visible={open} width={250}>
          <TableMenuItem menuItems={MENU_ITEMS} onMenuItemClick={onMenuItemClick} />
        </Drawer>
      </Card>
    </>
  );
};

export default HeadCountReport;
